a:hover{
  text-decoration: none;
}
::-webkit-scrollbar {
  width: 10px; /* ширина для вертикального скроллбара */
  height: 10px; /* высота для горизонтального скроллбара */
}

/* Стиль для ползунка скроллбара */
::-webkit-scrollbar-thumb {
  background-color: #ffffff; /* темно-сиреневый цвет фона */
  border-radius: 10px; /* закругленные края */
}
::-webkit-scrollbar-thumb:hover {
  background-color: #05333d; /* меняем цвет фона при наведении */
  transition: 3.5s;
  cursor: pointer;
}
/* Стиль для трека (дорожки) скроллбара */
::-webkit-scrollbar-track {
  background-color: lavender; /* светло-сиреневый цвет фона */
}
::-webkit-scrollbar {
  width: 10px; /* ширина для вертикального скроллбара */
  height: 10px; /* высота для горизонтального скроллбара */
}

/* Стиль для трека (дорожки) скроллбара */
::-webkit-scrollbar-track {
  background-color: #021b22; /* зеленый цвет фона */
}

/* Пример элемента, который будет иметь скроллбар */
.scrollable-content {
  height: 200px;
  overflow-y: scroll;
  border: 1px solid #021d23;
  padding: 10px;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
}
body {
  margin: 0;
  overflow-x: hidden !important;
  color: white;
}
@font-face {
  font-family: Kharkiv_Tone;
  src: url(./font/Kharkiv_Tone.ttf);
}
*{
  font-family: "Kharkiv_Tone";
}